<template>
	<div>
		<div class="breadcrumb-preview dy-breadcrumb-div">
			<el-breadcrumb :separator="'Ξ'"
				:style='{ "width": "1200px", "margin": "0 auto", "fontSize": "14px", "lineHeight": "1" }'>
				<el-breadcrumb-item>首页</el-breadcrumb-item>
				<el-breadcrumb-item>我的购物车</el-breadcrumb-item>
			</el-breadcrumb>
		</div>

		<div class="list-preview"
			:style='{ "margin": "0px auto", "flexWrap": "wrap", "background": "none", "display": "flex", "width": "1200px", "position": "relative", "justifyContent": "space-between" }'>
			<!-- 搜索区域 -->
			<el-form v-if="false" :inline="true" :model="formSearch" class="list-form-pv dy-list-form">
				<el-form-item v-if="true" :style='{ "margin": "0 10px" }'>
					<div v-if="true" class="lable" :style='{ "width": "auto", "padding": "0 10px", "lineHeight": "42px", "display": "inline-block" }'>零食</div>
					<el-select v-model="formSearch.shangpinid" placeholder="请选择零食" :clearable="true">
						<el-option label="全部" value=""></el-option>
						<el-option v-for="(item, index) in shangpinOptions" v-bind:key="item.id" :label="item.mingcheng" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-if="true" :style='{ "margin": "0 10px" }'>
					<div v-if="true" class="lable" :style='{ "width": "auto", "padding": "0 10px", "lineHeight": "42px", "display": "inline-block" }'>商品数量</div>
					<el-input suffix-icon="el-icon-search" v-model="formSearch.goumaishuliang" placeholder="商品数量" clearable></el-input>
				</el-form-item>
				<el-form-item v-if="false" :style='{ "margin": "0 10px" }'>
					<div v-if="true" class="lable" :style='{ "width": "auto", "padding": "0 10px", "lineHeight": "42px", "display": "inline-block" }'>用户</div>
					<el-select v-model="formSearch.usersid" placeholder="请选择用户" :clearable="true">
						<el-option label="全部" value=""></el-option>
						<el-option v-for="(item, index) in usersOptions" v-bind:key="item.id" :label="item.loginname" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>

				<el-button class="dy-search-button" type="primary" @click="search">
					<i :style='{"margin": "0 10px 0 0"}' class="el-icon-search"></i>查询</el-button>
			</el-form>

			<!-- 数据列表 -->
			<div class="list" :style='{ "width": "100%", "margin": "0", "background": "none", "order": "3" }'>
				<div class="table-content dy-list-div">
					<el-table class="tables" size="medium" :show-header="true" header-row-class-name="dy-list-el-table-header-row"
						header-cell-class-name="dy-list-el-table-header-cell"
						:style="{ width: '100%', fontSize: '14px', color: 'rgba(72, 72, 72, 1)' }" :data="dataList">
						<el-table-column align="center" label="序号" v-if="true" type="index" width="50" />
						<el-table-column align="center" header-align="center" label="图片">
							<template slot-scope="scope">
								<div v-if="scope.row.shangpin && scope.row.shangpin.image">
									<img :src="baseUrl + scope.row.shangpin.image" width="80" height="80">
								</div>
								<div v-else>暂无图片</div>
							</template>
						</el-table-column>
						<el-table-column align="center" header-align="center" label="零食" :show-overflow-tooltip="true">
							<template slot-scope="scope" v-if="scope.row.shangpin && scope.row.shangpin.mingcheng">
								{{ scope.row.shangpin.mingcheng }}
							</template>
						</el-table-column>
						<el-table-column align="center" header-align="center" label="商品数量">
							<template slot-scope="scope">
								<el-input-number v-if="scope.row.shangpin && scope.row.shangpin.kucunshuliang" :min="1" :max="scope.row.shangpin.kucunshuliang" @keydown.native="channelInputLimit" v-model="scope.row.goumaishuliang" @change="(currentValue, oldValue) => handleChange(currentValue, oldValue, scope.row.shangpin.jiage)" size="small" :step-strictly="true"></el-input-number>
								<div v-else>商品已失效</div>
							</template>
						</el-table-column>
						<el-table-column align="center" header-align="center" label="用户" :show-overflow-tooltip="true">
							<template slot-scope="scope" v-if="scope.row.users && scope.row.users.loginname">
								{{ scope.row.users.loginname }}
							</template>
						</el-table-column>

						<el-table-column align="center" header-align="center" label="单价">
							<template slot-scope="scope">
								<div v-if="scope.row.shangpin">{{ scope.row.shangpin.jiage }}</div>
                                <div v-else>商品已失效</div>
							</template>
						</el-table-column>
						<el-table-column align="center" header-align="center" label="操作" fixed="right">
							<template slot-scope="scope">
								<el-button type="danger" icon="el-icon-delete" size="mini"
									@click="handleDelete(scope.row.id)">删除</el-button>
							</template>
						</el-table-column>
					</el-table>
					<div class="buy" align="center" v-if="dataList.length > 0" style="margin: 30px;">
						<span style="color: red;margin-right: 10px;font-weight: 700;">总价：{{ totalAmount.toFixed(2)
						}}元</span>
						<el-button class="dy-add-button" type="success" @click="buyClick">提交订单</el-button>
						<el-button class="dy-back-button" @click="back()">返回</el-button>
					</div>
				</div>
			</div>
		</div>


		<el-pagination background class="pagination" :pager-count="7" :page-size="pageSize" :page-sizes="pageSizes"
			prev-text="上一页" next-text="下一页" :hide-on-single-page="true"
			:layout='["total", "prev", "pager", "next", "sizes", "jumper"].join()' :total="total"
			:style='{ "padding": "0", "margin": "20px auto", "whiteSpace": "nowrap", "color": "#333", "width": "1200px", "fontWeight": "500", "order": "4" }'
			@current-change="curChange" @prev-click="prevClick" @next-click="nextClick"
			@size-change="sizeChangeHandle"></el-pagination>

	</div>
</template>

<script>
export default {
	//数据集合
	data() {
		return {
			baseUrl: this.$config.baseUrl,
			formSearch: {
				key: '',
			},
			sortProperty: "id",
			selRows: [],
			totalAmount: 0,
			dataList: [],
			total: 1,
			pageSize: 1000,
			pageSizes: [8, 10, 20, 30, 50],
			totalPage: 1,
			xingbieOptions: [],
			typeOptions: [],
			shenheOptions: [],
			shangpinOptions: [],
			usersOptions: [],

		}
	},
	created() {
		this.init();
		this.getList();
	},
	computed: {
		totalPrice() {
			let totalPrice = 0;
			for (let x in this.selRows) {
				totalPrice += Number(this.selRows[x].shangpin.jiage) * Number(this.selRows[x].goumaishuliang)
			}
			return totalPrice.toFixed(2);
		}
	},
	//方法集合
	methods: {
		// 下载
		download(fileUrl) {
			let url = this.baseUrl + "common/download?filename=" + fileUrl;
			url = encodeURI(url);
			window.open(url);
		},
		curChange(page) {
			this.pageIndex = page;
			this.getList();
		},
		prevClick(page) {
			this.pageIndex = page;
			this.getList();
		},
		//pageSize触发
		sizeChangeHandle(val) {
			this.pageSize = val;
			this.pageIndex = 1;
			this.getList();
		},
		nextClick(page) {
			this.pageIndex = page;
			this.getList();
		},
		//点击搜索按钮方法
		search() {
			this.pageIndex = 1;
			this.getList();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
		handleSelectionChange(e) {
			this.selRows = e
		},
		channelInputLimit(e) {
			e.returnValue = '';
			e.returnValue.replace(/[^\u4E00-\u9FA5]/g, '');
		},
		//页面初始化
		init() {
			this.shenheOptions = "yes,no".split(',');
			this.xingbieOptions = "男,女".split(',');
			this.typeOptions = [{ value: '0', label: '管理' }, { value: '2', label: '用户' }];
			this.$http.get('users/session').then(({ data }) => {
				if (data && data.code === 0) {
					this.user = data.data;
					localStorage.setItem("users", data.data);
					localStorage.setItem("usersid", data.data.id);
				} else {
					let message = this.$message
					message.error(data.message);
				}
			});
			this.getShangpinList();
			this.getUsersList();

		},
		getList() {
			let params = {
				pageNo: this.pageIndex, //页码
				pageSize: this.pageSize, //页大小
				sortProperty: this.sortProperty,   //排序列
				usersid: localStorage.getItem('usersid'),
			}
			let searchWhere = {};
			this.$http.get('gouwuche/manamyqian', { params: Object.assign(params, searchWhere) }).then(res => {
				if (res.data.code == 0) {
					this.dataList = res.data.data.records;
					this.totalPage = res.data.data.pageCount;
					this.total = res.data.data.total;
				}
			});

			this.$http.get('gouwuche/totalamount', { params: Object.assign(params, searchWhere) }).then(res => {
				if (res.data.code == 0) {
					this.totalAmount = res.data.data;
				}
			});
		},
		handleChange(currentValue, oldValue, jiage) {
			// console.log('原先值：' + oldValue);
			// console.log('当前值：' + currentValue);
			if (currentValue > oldValue) {
				this.totalAmount = this.totalAmount + jiage;
			} else if (currentValue < oldValue) {
				this.totalAmount = this.totalAmount - jiage;
			}

		},
		handleDelete(id) {
			this.$confirm('是否确认删除?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				let delIds = new Array();
				delIds.push(id);
				this.$http.post('gouwuche/delete', delIds).then(res => {
					if (res.data.code == 0) {
						this.$message({
							type: 'success',
							message: '删除成功!',
							duration: 1500,
							onClose: () => {
								this.getList();
							}
						});
					}
				});
			});
		},
		toDetail(id) {
			this.$router.push({ path: '/index/shangpinDetail', query: { id: id } });
		},
		buyClick() {
			// if (this.selRows.length == 0) {
			// 	this.$message({
			// 		type: 'warning',
			// 		message: '请选择商品再提交订单',
			// 		duration: 1500,
			// 	});
			// 	return
			// }

			// localStorage.setItem('orderGoods', JSON.stringify(this.selRows));
			// this.$router.push({ path: '/index/dingdanAdd', query: { jine: this.totalPrice } });

			let params = [];
			this.dataList.map((item) => {
				params.push({ id: item.id, goumaishuliang: item.goumaishuliang })
			});
			console.log(params);
			this.$http.post('gouwuche/submitdingdan', params).then(res => {
				if (res.data.code == 0) {
					this.$message({
						type: 'success',
						message: '提交订单成功!',
						duration: 400,
						onClose: () => {
							this.$router.push({ path: '/index/dingdanAdd', query: { jine: this.totalAmount } });
						}
					});
				}
			});
		},
		//获取所有零食
		getShangpinList() {
			this.$http.get('shangpin/all',).then(res => {
				if (res && res.data.code == 0) {
					this.shangpinOptions = res.data.data;
				}
			});
		},
		//获取所有用户
		getUsersList() {
			this.$http.get('users/all',).then(res => {
				if (res && res.data.code == 0) {
					this.usersOptions = res.data.data;
				}
			});
		},

	}
}
</script>

